.page {
   width: 100%;
   position: fixed;
   height: 100%;
   overflow: var(--overflowStyles);
   transition-property: opacity, transform;
   transition: 300ms ease-out;
   top: 0;
}

.page-enter {
   opacity: 0;
   transform: translateX(100%);
}

.page-enter-active {
   opacity: 1;
   transform: translateX(0, 0);
}

.page-exit-active {
   opacity: 0;
   transform: translateX(-100%);
}
