.Navbar {
  display: flex;
  height: 5%;
  align-items: center;
  justify-content: space-between;
  background: #fff;
}

/* Slider */

.Logo {
  display: block;
  flex: 0 0 10%;
  background: #dfdfdf;
  padding: 1.5rem 2rem;
  text-align: center;
}

.Logo a {
  text-decoration: none;
  color: #444;
}
.Logo a:hover {
  color: #000;
}

.Slider {
  display: flex;
  flex: 1 0 70%;
}

.Slider p {
  margin: 0 1.5rem;
  font-size: 1.4rem;
}

.Slider span {
  font-weight: bold;
}

.Selection {
  margin: 0 2.5rem;
}

.MuiButtonBase-root,
.MuiInputBase-root {
  font-size: 1.4rem !important;
}

.rc-slider {
  width: 50%;
  display: flex;
  margin-right: auto;
  align-items: center;
}

.rc-slider-track {
  background: #b607ff;
}

.rc-slider-handle,
.rc-slider-handle:focus,
.rc-slider-handle:active,
.rc-slider-handle:hover {
  border: 2px solid #b607ff;
  background: #fff;
  margin: 0;
  box-shadow: none;
}

@media only screen and (max-width: 767.98px) {
  .Navbar {
    flex-direction: column;
    height: 5%;
    align-items: stretch;
    justify-content: center;
  }

  .Slider {
    display: none;
  }
  .Selection {
    display: none;
  }
}

.Palette {
  height: 100vh;
}
.Palette-colors {
  height: 90%;
  background: #fff;
}

/* Footer */

.palette-footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  border-top: 1px solid rgba(0, 0, 0, 0.4);
  height: 5%;
  background: #fff;
}

.palette-footer h3 {
  font-weight: lighter;
  margin: 0.5rem 1.5rem;
  font-size: 1.5rem;
}

.page {
   width: 100%;
   position: fixed;
   height: 100%;
   overflow: var(--overflowStyles);
   -webkit-transition-property: opacity, -webkit-transform;
   transition-property: opacity, -webkit-transform;
   transition-property: opacity, transform;
   transition-property: opacity, transform, -webkit-transform;
   -webkit-transition: 300ms ease-out;
   transition: 300ms ease-out;
   top: 0;
}

.page-enter {
   opacity: 0;
   -webkit-transform: translateX(100%);
           transform: translateX(100%);
}

.page-enter-active {
   opacity: 1;
   -webkit-transform: translateX(0, 0);
           transform: translateX(0, 0);
}

.page-exit-active {
   opacity: 0;
   -webkit-transform: translateX(-100%);
           transform: translateX(-100%);
}

